
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default {
name: 'coupons',
components: {
Group,
XSwitch
},
data () {
return {
aa: true,
list: []
}
},
created () {
this.getData()
},
methods: {
changeList (data,index) {
let operate = 0
if(data.status == 0) {
operate = 3
} else {
operate = 0
}
this.$http.fetchGet('/merchant/post/update/coupon/status',{couponId:data.couponId,operate:operate}).then((res)=>{
if(res.data.code == 200) {
this.list[index].status = operate
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},
addCoupons () {
this.$router.push({
name: 'addCoupons'
})
},
getBack () {
this.$router.go(-1)
},
getData () {
let params = {
page: 1,
pageSize: 100,
merchantId: this.$store.state.merchantId
}
this.$http.fetchGet('/merchant/post/get/couponlist', params).then((res) => {
this.list = res.data.data.coupons
this.list.map((item)=>{
if(item.status == 0) {
item.isUsed = true
} else {
item.isUsed = false
}
})
})
}
}
}



